@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;700&display=swap);
*{font-family:'Barlow', sans-serif}

.app{width:100vw;height:90vh;margin-top:10vh;display:flex;align-items:center;flex-direction:column}.app .logo{margin-bottom:10px}.app .footer p{color:#C4C4C4;font-size:12px;text-transform:uppercase;text-align:center;margin-top:30px;font-weight:300}@media (max-width: 900px){.app{margin-top:5vh;height:95vh}}

.container{width:800px;background-color:#E0E6EE;display:flex;flex-direction:column;padding:30px;border-radius:8px}@media (max-width: 900px){.container{width:80%}}

.collapsible-card{display:flex;flex-direction:column;align-items:center;width:100%}.collapsible-card .title{color:#00687F;font-weight:700}.collapsible-card .collapsible-card-button button{border:none;outline:none;display:flex;justify-content:center;align-items:center;width:30px;height:30px;background-color:#fff;cursor:pointer}.collapsible-card .open{height:100%;flex-direction:column;transform:translateY(-20px);z-index:0;box-shadow:0px 2px 1px #c4c4c433;border-radius:0}

.card{width:100%;max-width:730px;height:15px;display:flex;justify-content:space-between;align-items:center;margin:8px 0;font-size:16px;padding:15px;background-color:#fff;box-shadow:0px 4px 12px #c4c4c433;border-radius:4px;z-index:1}

.planets{width:100%}.planets .spinner-wrapper{display:flex;justify-content:center;align-items:center}

.spinner img{animation:rotate 1s linear infinite}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.planets-table{width:100%;text-align:right}.planets-table table{border-collapse:collapse;width:100%;color:#474747;font-size:12px}.planets-table table thead{border-bottom:1px solid #C4C4C4}.planets-table table thead tr th{width:50px;font-weight:400;padding:10px 20px}.planets-table table thead tr th:first-child{white-space:nowrap;text-align:left;color:#00687F}.planets-table table thead tr th .header-content{display:flex;justify-content:flex-end;align-items:center}.planets-table table thead tr th .header-content .arrow{color:#474747;font-size:1px;padding:0 5px}.planets-table table thead tr th .header-content .arrow .teal{color:#00687F}.planets-table table tbody tr td{padding:20px}.planets-table table tbody tr td:first-child{text-align:left;color:#00687F}

.planets-small-screen .planet-card{margin:20px 0}.planets-small-screen .planet-card:nth-child(even){background-color:#ebebeb}.planets-small-screen .planet-card .row{display:flex;padding:10px}.planets-small-screen .planet-card .row .column{width:50%}.planets-small-screen .planet-card .row .column:first-child{margin-right:10px}.planets-small-screen .planet-card .row .teal{color:#00687F}

.divider{border:1px dashed #fff;margin:25px 0}

.input{display:flex;flex-direction:column;position:relative}.input input{border:none;border-bottom:1px solid #474747;outline:none;color:#474747;padding:5px 0}.input input::-webkit-input-placeholder{color:#C4C4C4}.input input:-ms-input-placeholder{color:#C4C4C4}.input input::placeholder{color:#C4C4C4}.input label{font-size:12px;color:#474747}.input img{position:absolute;right:5px;bottom:5px;background-color:#fff}.input .error{border:1px solid #FF1616;margin-top:1px;padding:8px;color:#FF1616;box-shadow:0px 4px 4px #c4c4c480;font-size:12px}.input .red{color:#FF1616}

.button button{background-color:#1BA1BE;color:#fff;text-transform:uppercase;border-radius:4px;font-weight:700;font-size:12px;letter-spacing:1px;border:none;height:33px;padding:10px;cursor:pointer;outline:none}.button button:disabled{background-color:#E0E6EE;cursor:default}

.add-movie-form{width:80%;margin-top:20px}.add-movie-form .input-wrapper{margin-bottom:20px;position:relative}.add-movie-form .planet-input{margin-top:20px}.add-movie-form .button{display:flex;justify-content:flex-end}.add-movie-form .button button{width:160px;margin-top:10px}

.magic-dropdown{border:1px solid #E5E5E5;color:#474747;box-shadow:0px 4px 4px #c4c4c480;position:absolute;width:100%;background-color:#fff;transform:translateY(1px);padding:10px 0}.magic-dropdown .planet-name{padding:5px 10px;cursor:pointer}.magic-dropdown .planet-name:hover{background-color:#ebebeb}

.spinner-wrapper{text-align:center}

.cancellable-button button{border:1px solid #474747;border-radius:18px;background-color:#fff;padding:6px 16px;color:#474747;cursor:pointer;outline:none;display:flex;justify-content:space-between;align-items:center}.cancellable-button button:hover{background-color:#ebebeb}.cancellable-button button .cross{margin-left:10px;font-weight:700;font-size:12px;color:#474747}

.chosen-planets{display:flex;flex-wrap:wrap}.chosen-planets .button-wrapper{margin:0 5px 5px}

