@import "../../../variables.scss";

.button {
	
	button {
		background-color: $light-teal;
		color: $white;
		text-transform: uppercase;
		border-radius: 4px;
		font-weight: $bold;
		font-size: $m;
		letter-spacing: 1px;
		border: none;
		height: 33px;
		padding: 10px;
		cursor: pointer;
		outline: none;

		&:disabled {
			background-color: $gray;
			cursor: default;
		}
	}
}